:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

button {
  min-width: 100px;
  cursor: pointer;
  background: olive;
  border: 0;
  border-radius: 5px;
  padding: 8px 24px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

button:hover, button:focus {
  background: #6b8e23;
}

.search-form-container {
  width: 100%;
  z-index: 3;
  background-color: #dcdcdc;
  position: fixed;
  top: 0;
}

.search-form {
  text-align: center;
  max-width: 600px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
}

input {
  border: 2px solid #add8e6;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 14px;
}

input:focus {
  border: 2px solid gray;
  outline: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  max-width: 360px;
  height: 370px;
  transition: box-shadow var(--animation-duration) var(--timing-function), scale var(--animation-duration) var(--timing-function);
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

.photo-card:hover {
  scale: 1.1;
  box-shadow: 0 1px 6px rgba(128, 160, 170, .91), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.photo {
  width: 360px;
  height: 290px;
  object-fit: cover;
  border-radius: 5px;
  display: block;
}

.info {
  text-align: center;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.info-item {
  color: #212121;
  font-size: 16px;
}

.load-more {
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.hide {
  display: none;
}

