:root {
    --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    --animation-duration: 250ms;
  }
  
  * {
    box-sizing: border-box;
  }
  a {
    text-decoration: none;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fff;
    color: #212121;
    font-size: 18px;
    line-height: 1.5;
  }
  
  button {
    min-width: 100px;
    background: olive;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border: 0px;
    border-radius: 5px;
    font-family: inherit;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
    cursor: pointer;
    padding: 8px 24px;

  }
  button:hover,
  button:focus {
    background: olivedrab;
  }
  .search-form-container {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: gainsboro;
    z-index: 3;
  }
  .search-form {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    max-width: 600px;
    margin-bottom: 16px;
  }
  input {
    border-radius: 4px;
    padding: 8px 16px;
    border: 2px solid rgb(173, 216, 230);
    font-size: 14px;
  }
  input:focus {
    border: 2px solid gray;
    outline: none;
  }
  
  .gallery {
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }
  
  .photo-card {
    background-color: #fff;
    max-width: 360px;
    height: 370px;
    border-radius: 5px;
    box-shadow: 0px 1px 6px rgba(46, 47, 66, 0.08);
    transition: box-shadow var(--animation-duration) var(--timing-function),
      scale var(--animation-duration) var(--timing-function);
  }
  .photo-card:hover {
    box-shadow: 0px 1px 6px rgba(128, 160, 170, 0.91),
      0px 1px 1px rgba(46, 47, 66, 0.16), 0px 2px 1px rgba(46, 47, 66, 0.08);
    scale: 1.1;
  }
  .photo {
    display: block;
    width: 360px;
    height: 290px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .info {
    display: flex;
    text-align: center;
    justify-content: center;
    gap: 20px;
  }
  .info-item {
    font-size: 16px;
    color: #212121;
  }
  
  .load-more {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
  }
  .hide {
    display: none;
  }